import React from 'react';
import firebase from '../components/firebase';
import { useCollection } from 'react-firebase-hooks/firestore';

export default function MobileApplication(props){
    const [value, loading, error] = useCollection(
        firebase.firestore().collection('projects').where("cat", "==", "app"),
        {
          snapshotListenOptions: { includeMetadataChanges: true },
        }
    );

    const Download = (link) => {
        window.location.href = link;
    }

    return(
        <div className="container-fluid">
            <div className="text-center">
                <h2>Mobile Application</h2>
                <h4>Choose a payment plan that works for you</h4>
            </div>
            {error && <strong>Error: {JSON.stringify(error)}</strong>}
            {loading && <span>Projects: Loading...</span>}
            {value && value.docs.map(doc => 
                <div className="panel panel-default" key={doc.id}>
                    <div className="panel-body">
                        <h3>{doc.data().title}</h3>
                        <p>{doc.data().description}</p>
                        <br/>
                        <button className="btn green-btn" onClick={() => Download(doc.data().source)}>Downlaod Code</button>&nbsp;
                        <button className="btn green-btn"
                            onClick={() => Download(doc.data().apk)}
                        >Downlaod APK</button>
                    </div>
              </div>
            )}
        </div>
    )
}