import React from 'react';
import firebase from './firebase';
import { useCollection } from 'react-firebase-hooks/firestore';
import { Link } from 'react-router-dom';


export default function WebDevelopment(props){

    const [value, loading, error] = useCollection(
        firebase.firestore().collection('projects').where("cat", "==", "web").limit(3),
        {
          snapshotListenOptions: { includeMetadataChanges: true },
        }
    );

    return(
        <div id="web-development" className="container-fluid bg-grey">
            <div className="text-center">
                <h2>Web Development</h2>
                <h4><Link to="web-development">View All Web Development Projects</Link></h4>
            </div>
            <div className="row slideanim">
                {error && <strong>Error: {JSON.stringify(error)}</strong>}
                {loading && <span>Collection: Loading...</span>}
                {value && value.docs.map(doc => 
                    <div className="col-sm-4 col-xs-12" key={doc.id}> 
                    <div className="panel panel-default text-center">
                        <div className="panel-heading">
                        <h3>{doc.data().title}</h3>
                        </div>
                        <div className="panel-footer">
                        <p>{doc.data().description}</p>
                        <button className="btn btn-lg">Download</button>
                        </div>
                    </div>      
                </div>       
                )}
            </div>
        </div>

    )
}