import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


// import component
import Menu from './components/Menu';

// import pages
import MainPage from './pages/MainPage';
import IoT from './pages/IoT';
import MobileApplication from './pages/MobileApplication';
import WebDevelopment from './pages/WebDevelopment';


function App() {
  return (
    <React.Fragment>
      <Router>
        <Menu/>
        <Switch>
          <Route path="/" exact>
            <MainPage />
          </Route>
          <Route path="/internet-of-things">
            <IoT/>
          </Route>
          <Route path="/mobile-application">
            <MobileApplication />
          </Route>
          <Route path="/web-development">
            <WebDevelopment/>
          </Route>
        </Switch>
      </Router>
    </React.Fragment>
  );
}

export default App;
